.current {
	color: green;
}

body {
	font-family: Verdana, sans-serif;
	font-size: 12pt;
	max-width: 720px;
	margin-left: 10em;
	margin-right: 10em;
}

img {
	max-width: 720px;
	display: block;
	margin: 0 auto;
}

table {
  border: thin solid black;
  border-collapse: collapse;
  padding: 0.4rem;
	margin-left: auto;
	margin-right: auto;
}

table thead th { border-bottom: 1px solid #000; }

th, td {
	padding: 0.4rem;
}

blockquote {
  background: #f9f9f9;
  border-left: 10px solid #ccc;
  margin: 1.5em 10px;
  padding: 0.5em 10px;
}
blockquote:before {
  color: #ccc;
  font-size: 4em;
  line-height: 0.1em;
  margin-right: 0.25em;
  vertical-align: -0.4em;
}
blockquote p {
  display: inline;
}
